



















import Vue from "vue";
export default Vue.extend({
	mounted() {
		this.$anime.timeline({ loop: false }).add({
			targets: ".hero",
			translateY: [100, 0],
			translateZ: 0,
			opacity: [0, 1],
			easing: "easeOutExpo",
			duration: 1400,
			delay: (el, i) => 300 + 30 * i
		});
	}
});
